import { memo, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Axios from "axios";

const DetailPage = () => {
  const { pid } = useParams();
  //const [searchParams, setSearchParams] = useSearchParams();
  //const id = searchParams.get("id");

  const [qData, setQData] = useState([
    {
      liquid_id: "",
      liquid_company: "",
      liquid_name: "",
      liquid_type: "",
      liquid_price: 0,
      liquid_size: 0,
      liquid_sweet: 0,
      liquid_cool: 0,
      liquid_hit: 0,
      liquid_category1: "",
      liquid_category2: "",
      keywords: "",
      item_img: "",
      liquid_discount: 0,
    },
  ]);

  useEffect(() => {
    // 데이터를 받아오는 동안 시간이 소요되므로 await 로 대기
    async function fetchData() {
      try {
        // 데이터를 받아오는 동안 시간이 소요되므로 await 로 대기
        const res = await Axios.get(
          `http://localhost:5000/api/liqs?liqId=VA_L${pid.padStart(5, "0")}`
        );
        // 받아온 데이터로 다음 작업을 진행하기 위해 await 로 대기
        const _inputData = await res.data.map((rowData) => ({
          liquid_id: rowData.idx,
          liquid_company: rowData.liquid_company,
          liquid_name: rowData.liquid_name,
          liquid_type: rowData.liquid_type,
          liquid_price: rowData.liquid_price,
          liquid_size: rowData.liquid_size,
          liquid_sweet: rowData.liquid_sweet,
          liquid_cool: rowData.liquid_cool,
          liquid_hit: rowData.liquid_hit,
          liquid_category1: rowData.liquid_category1,
          liquid_category2: rowData.liquid_category2,
          keywords: rowData.keywords,
          item_img: rowData.item_img,
          liquid_discount: rowData.liquid_discount,
        }));
        // 선언된 _inputData 를 최초 선언한 inputData 에 concat 으로 추가
        setQData(_inputData);
      } catch (e) {
        console.error(e.message);
      }
    }
    fetchData();
  }, [pid]);

  let val = 3.141592;
  let review = 190;

  const url = "https://ando-buck.s3.ap-northeast-2.amazonaws.com";

  return (
    <>
      {qData[0] ? (
        <>
          <div>
            <img
              width={258.36}
              src={url + qData[0].item_img}
              className="mx-auto"
              alt=""
            />
          </div>
          <div className="">
            <p
              // 제조사
              className="font-medium"
            >
              {qData[0].liquid_company}
            </p>
            <p
              //제목
              className="mt-[3px]"
            >
              {qData[0].liquid_name}
            </p>
            {/* <p
          className="mt-[4px]"
          //설명
        >
          맛설명란 맛설명란
        </p> */}
            <p
              //가격
              className="mt-[8px] font-bold"
            >
              {qData[0].liquid_price.toLocaleString("ko-KR")}원
            </p>
            <p
              // 별점 / 리뷰
              className="h-[28px] mt-[4px] text-12px"
            >
              <span className="relative text-[#EEEEEE]">
                ★★★★★
                <span
                  className="text-[#FF8E00] absolute left-0 overflow-hidden"
                  style={{ width: `${val * 20}%` }}
                >
                  ★★★★★
                </span>
              </span>
              <span className="ml-[4px]">{val} / </span>
              <span className="underline">{review}개의 리뷰</span>
            </p>
            <hr className="mt-[32px]" />
            {/* 여기에 통 이미지 삽입 */}
            <img src="/img/detail_image_example.png" alt="제품 설명 이미지" />
            <div className="w-full max-w-sm h-[81px] bg-white flex fixed bottom-0 z-20">
              <div className="mt-[12px] mb-[17px] w-full relative">
                <button className="bg-white border border-[#E6E6E6] rounded-[8px] w-[161px] h-[52px] absolute left-[16px]">
                  장바구니
                </button>
                <button
                  className="bg-black text-white rounded-[8px] w-[161px] h-[52px] absolute right-[16px]"
                  onClick={() => {
                    // 실제로 상품 필터 적용하는 코드
                  }}
                >
                  바로구매
                </button>
              </div>
            </div>
          </div>
        </>
      ) : (
        <h2>해당 페이지는 존재하지 않습니다.</h2>
      )}
    </>
  );
};

export default memo(DetailPage);
