import LandingMain from "../components/LandingPage/p0-LandingMain";
import Tobacc from "../components/LandingPage/p1-Tobacc";
import Cigar from "../components/LandingPage/p2-Cigar";
import Liquid from "../components/LandingPage/p3-Liquid";
import { useParams } from "react-router-dom";

export default function Landing() {
  const page_num = parseInt(useParams()["page"], 10);

  let renderPage;
  switch (page_num) {
    case 1:
      renderPage = <Tobacc />;
      break;
    case 2:
      renderPage = <Cigar />;
      break;
    case 3:
      renderPage = <Liquid />;
      break;
    case 4:
      renderPage = <div>공사중!</div>;
      break;
    default:
      renderPage = <LandingMain />;
  }

  return <div className="mt-[-64px]">{renderPage}</div>;
}
