import { memo, useState } from "react";

import Axios from "axios";
import { useNavigate } from "react-router-dom";

let liqData = [];

const sortOptions = ["BEST", "NEW", "낮은 가격순", "높은 가격순"];

let parts = new Array(10).fill(0);

const ShoppingPage = () => {
  const nav = useNavigate();
  const [toggleSorter, setToggleSorter] = useState(false); // 정렬 드롭다운 토글 여부
  const [sortModeText, setSortModeText] = useState("BEST"); // 선택된 정렬 옵션 텍스트
  const [sortMode, setSortMode] = useState("best"); // 선택된 정렬 옵션

  const setSortOption = (text) => {
    let mode;
    switch (text) {
      case "BEST":
        mode = "best";
        break;
      case "NEW":
        mode = "new";
        break;
      case "낮은 가격순":
        mode = "cheap";
        break;
      case "높은 가격순":
        mode = "expensive";
        break;
      default:
        mode = "best";
        break;
    }
    setSortMode(mode);
    setSortModeText(text);
    setToggleSorter(false);
  };

  // const applyFilterOption = (text) => {
  //   setSelectedFilterOption(text);
  //   findItems();
  //   return;
  // };

  return (
    <>
      <div className="h-[300px] bg-red-300">뭔가.. 뭔가가 들어가고 이씀</div>
      <div className="grid grid-cols-3 gap-[14px] mx-[16px] my-[30px]">
        {[
          { str: "ALL", url: "all" },
          { str: "액상", url: "liquid" },
          { str: "기기", url: "device" },
          { str: "액세서리", url: "accessory" },
          { str: "스타터세트", url: "starter" },
        ].map((type, itr) => {
          return (
            <p
              className="mx-auto w-[100px] h-[50px] bg-[#F6F6F6] flex flex-col justify-center"
              // 구현이 안 돼서 오렌지 배경으로 강조
              style={{
                backgroundColor: `${itr === 3 || itr === 4 ? "orange" : null}`,
              }}
              key={type.str}
              onClick={() => {
                nav(type.url);
              }}
            >
              {type.str}
            </p>
          );
        })}
      </div>
      <hr className="border-[#EBEBEB]" />
      <div className="mt-[30px] mx-[16px]">
        <div className="flex justify-between relative h-[40px]">
          <div className="font-medium self-center">안도에서 만나보세요</div>
          <div
            className="w-[130px] text-14px rounded-[25px] border-[#F1F1F1] border-[2px] bg-white px-[16px] py-[4px] absolute right-0 z-10"
            onClick={() => setToggleSorter(!toggleSorter)}
          >
            <div className="flex justify-between py-[2px]">
              <p>{sortModeText}</p>
              <p className="self-center">
                <img
                  src="/img/sort_updown.svg"
                  width={14}
                  alt="sort: updown arrow"
                />
              </p>
            </div>
            {toggleSorter && (
              <>
                <hr className="border-[#F1F1F1] mt-[8px]" />
                {sortOptions.map((elem, itr) => {
                  return (
                    <p
                      className="py-[4px]"
                      key={"shop_sort_" + itr}
                      onClick={() => setSortOption(elem)}
                    >
                      {elem}
                    </p>
                  );
                })}
              </>
            )}
          </div>
        </div>
        <div className="mt-[16px] text-left text-14px grid grid-cols-2 gap-[12px]">
          {parts.map((part, itr) => (
            <div className="mb-[12px] flex flex-col" key={"product_" + itr}>
              <div className="bg-[#F5F5F5] w-full relative">
                {/* 지금은 찜 유무를 랜덤으로 뒀음.
                 나중에 세션으로 유저 정보 -> 찜 정보 받아올 수 있을 때
                 활성화해서 사용하시오 */}
                {Math.round(Math.random()) ? (
                  <img
                    src="/imageDB/common/heartFull.svg"
                    width={14.26}
                    height={12.5}
                    className="absolute top-[9.5px] right-[7.87px]"
                    alt="찜한 상품"
                  />
                ) : (
                  <img
                    src="/imageDB/common/heartEmpty.svg"
                    width={14.26}
                    height={12.5}
                    className="absolute top-[9.5px] right-[7.87px]"
                    alt="찜하지 않은 상품"
                  />
                )}
                <p className="my-[12px]">
                  <img
                    src="/imageDB/liq_trace/Novo-MSBlend.png"
                    width={138}
                    className="mx-auto"
                    alt=""
                  />
                </p>
              </div>
              <div className="mt-[12px] text-left tracking-[-0.28px]">
                <p className="text-14px font-medium">노보</p>
                <p className="text-14px">엠에스 블랜드</p>
                <p className="font-bold">20,000</p>
                <p className="mt-[4px] py-[1px] px-[4px] text-10px font-bold w-min bg-[#E6F0FF] text-[#0060F1]">
                  하루배송
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default memo(ShoppingPage);
