import { memo, useEffect, useState } from "react";
import ItemCard from "../components/mainPage/itemCard";

import Axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronDown,
  faChevronUp,
  faSearch,
} from "@fortawesome/free-solid-svg-icons";

let liqData = [];

const sortOptions = ["등록일순", "인기순", "가격 낮은순", "가격 높은순"];
const filterOptions = {
  all: [],
  liquid: ["과일", "디저트", "음료", "연초"],
  machine: [],
  consumable: ["팟", "코일"],
  disposable: [],
  sale: [],
};

const ShoppingSpecific = () => {
  const [search, setSearch] = useState(""); // 검색창 입력 텍스트
  // const [found, setFound] = useState([]); // 검색된 아이템 [리스트]
  const parts = ["감귤", "프룬", "포도", "사과"];

  const [toggleSorter, setToggleSorter] = useState(false); // 정렬 드롭다운 토글 여부
  const [sortMode, setSortMode] = useState("recent"); // 선택된 정렬 옵션
  const [sortModeText, setSortModeText] = useState("등록일순"); // 선택된 정렬 옵션 텍스트

  const [selectedMenu, setSelcetedMenu] = useState("liquid"); // 선택된 아이템 카테고리     기존 : all
  const [selectedFilterOption, setSelectedFilterOption] = useState(""); // 선택된 필터링 옵션

  useEffect(() => {
    Axios.get("http://localhost:5000/api/liqs").then((response) => {
      liqData = response.data;
      // setFound(liqData);
      // console.log(liqData);
    });
  }, []);

  const findItems = () => {
    let tempFound = liqData.filter((item) => item["keywords"].includes(search));
    if (selectedFilterOption && selectedFilterOption !== "") {
      // 아래 부분은 디비가 생겼을 경우, 태그 등에서 검색 해야함.
      tempFound = tempFound.filter((item) =>
        item["keywords"].includes(selectedFilterOption)
      );
    }
    console.log("temp 데이터 : " + tempFound);
    // setFound(tempFound);
    return;
  };

  const setSortOption = (text) => {
    let mode;
    switch (text) {
      case "등록일순":
        mode = "recent";
        break;
      case "인기순":
        mode = "popular";
        break;
      case "가격 낮은순":
        mode = "cheap";
        break;
      case "가격 높은순":
        mode = "expensive";
        break;
      default:
        mode = "recent";
        break;
    }
    setSortMode(mode);
    setSortModeText(text);
    setToggleSorter(false);
  };

  const applyFilterOption = (text) => {
    setSelectedFilterOption(text);
    findItems();
    return;
  };

  // console.log(sortMode);

  return (
    <div //쇼핑 페이지
      className="py-2 mx-[20px]"
    >
      <div
        //서칭폼 구역
        className="bg-[#F0F0F0] rounded-[30px] flex h-11"
      >
        <input
          // className="bg-transparent w-[270px] h-11 ml-[20px]"
          className="bg-transparent w-10/12 h-11 ml-[20px]"
          width="80%"
          type="text"
          name="searchText"
          placeholder="제품명으로 검색하기"
          onChange={({ target: { value } }) => setSearch(value)}
        />
        {/* 돋보기 */}
        <div className="flex items-center w-2/12" onClick={findItems}>
          <FontAwesomeIcon icon={faSearch} className="mx-auto" />
        </div>
      </div>
      <div
        //카테고리 구역
        className="mt-[12px] flex justify-between text-[#666666] text-14px"
      >
        <div
          onClick={() => {
            setSelcetedMenu("all");
          }}
        >
          전체 상품
        </div>
        <div
          onClick={() => {
            setSelcetedMenu("liquid");
          }}
        >
          액상
        </div>
        <div
          onClick={() => {
            setSelcetedMenu("machine");
          }}
        >
          기기
        </div>
        <div
          onClick={() => {
            setSelcetedMenu("consumable");
          }}
        >
          소모품
        </div>
        <div
          onClick={() => {
            setSelcetedMenu("disposable");
          }}
        >
          일회용
        </div>
        <div
          onClick={() => {
            setSelcetedMenu("sale");
          }}
        >
          재고할인
        </div>
      </div>
      <div
        //공백 구역
        className="w-full bg-[#F5F5F5] h-2 my-2"
      />
      <div
        //필터링 구역
        className="flex my-2 relative h-[50px] py-2"
      >
        <div
          // 드롭다운 영역
          className="w-[98px] px-[10px] rounded-[17.5px] bg-[#3268FF] text-14px text-white absolute"
        >
          <div
            className="py-2 h-[35px]"
            onClick={() => setToggleSorter(!toggleSorter)}
          >
            {sortModeText}{" "}
            {toggleSorter ? (
              <FontAwesomeIcon icon={faChevronUp} />
            ) : (
              <FontAwesomeIcon icon={faChevronDown} />
            )}
          </div>
          {toggleSorter && (
            <>
              <hr className="border-[#1549FF]" />
              {sortOptions.map((it) => {
                return (
                  <div className="py-[4px]" onClick={() => setSortOption(it)}>
                    {it}
                  </div>
                );
              })}
            </>
          )}
        </div>
        <div className="flex left-[101px] h-[35px] rounded-[17.5px] absolute">
          {
            // 아래 부분 카테고리에 따라서 나타나야함..
            // filterOptions[{ selectedMenu }].map((it) => {
            filterOptions["liquid"].map((it, idx) => {
              return (
                <div
                  className="ml-[3px] text-14px rounded-[17.5px] border-[#F1F1F1] px-[10px] py-[4px] border-[2px]"
                  onClick={() => applyFilterOption(selectedMenu)}
                  key={"fliter_liq_" + idx}
                >
                  {it}
                </div>
              );
            })
          }
        </div>
      </div>
      <div className="mt-[16px] text-left text-14px grid grid-cols-2 gap-[12px]">
        {parts.map((part, i) => (
          <div className="mb-[12px] flex flex-col">
            <div className=" bg-[#F5F5F5] w-full">
              <p className="my-[12px]">
                <img
                  src="/imageDB/liq_trace/Novo-MSBlend.png"
                  width={138}
                  className="mx-auto"
                  alt=""
                />
              </p>
            </div>
            <div className="mt-3 text-left tracking-[-0.28px]">
              <p className="text-14px font-medium">노보</p>
              <p className="text-14px">엠에스 블랜드</p>
              <p className="font-bold">20,000</p>
              <p className="mt-[4px] py-[1px] px-[4px] text-10px font-bold w-min bg-[#E6F0FF] text-[#0060F1]">
                하루배송
              </p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default memo(ShoppingSpecific);
