import { useParams } from "react-router-dom";

export default function ReviewPage() {
  const { pid } = useParams();

  let users = ["김*영", "이*준", "최*우", "박*성", "조*결"];
  let profile_pic = [
    "https://bit.ly/3G8EpAP",
    "https://bit.ly/3hKMz9c",
    "https://bit.ly/3hDGbk8",
    "https://bit.ly/3BWLtOy",
    "https://bit.ly/3v6yCFE",
  ];
  let seed = Math.floor(Math.random() * users.length);
  let seed_index = [];
  for (let iterator = 0; iterator < users.length; iterator++) {
    seed_index.push((seed + iterator) % users.length);
  }

  let review_logs = [
    { str: "중요한 것은 꺾이지 않는 마음입니다.", stars: 4.259 },
    { str: "제가 찾던 액상 여기있었네요 ^^*", stars: 5.0 },
    {
      str: "이거 피우고 자신감이 생ㄱ기고 여자친구가 생겼습니다! 겠냐고~",
      stars: 1,
    },
    {
      str: "5점 만점에 10점!",
      stars: 2,
    },
    {
      str: "진짜 맛있음. 진짜 맛있는 빵 + 과자 먹는 느낌",
      stars: 4.62,
    },
  ];

  let review_date = "2022.11.10";

  const Modal = ({ log, itr }) => (
    <>
      <div className="mt-[24px] mx-[16px] flex justify-between">
        <div className="flex">
          <img
            src={profile_pic[itr]}
            className="mx-auto h-[40px] w-[40px]"
            alt="avatar"
          />
          <div className="ml-[15px] text-14px">
            <p className="font-medium">{users[itr]}님</p>
            <p className="text-[#878787] Roboto">{review_date}</p>
          </div>
        </div>
        <div>
          <span className="relative text-[#EEEEEE]">
            ★★★★★
            <span
              className="text-[#FF8E00] absolute left-0 overflow-hidden"
              style={{ width: `${log.stars > 5 ? 100 : log.stars * 20}%` }}
            >
              ★★★★★
            </span>
          </span>
        </div>
      </div>

      <div className="mt-[19px] mb-[30px] mx-[16px] bg-[#F6F6F6] py-[12px] px-[12px] text-[12px]">
        {log.str}
      </div>
    </>
  );

  return (
    <>
      <div>총 {review_logs.length.toLocaleString("ko-KR")}개의 리뷰</div>
      <hr className="mt-[16px] border-[#EBEBEB]" />
      <div className="text-left">
        {review_logs.map((elem, itr) => (
          <div key={"review_" + pid + "_" + itr}>
            <Modal log={elem} itr={seed_index[itr]} />
            {itr !== review_logs.length - 1 ? <hr /> : null}
          </div>
        ))}
      </div>
    </>
  );
}
