import { useState } from "react";

import {
  faChevronDown,
  faChevronRight,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function ShopDevice(params) {
  const sortOptions = ["BEST", "NEW", "낮은 가격순", "높은 가격순"];
  const [toggle_sort, setToggleSorter] = useState(false); // 정렬 드롭다운 토글 여부
  const [sortModeText, setSortModeText] = useState("BEST"); // 선택된 정렬 옵션 텍스트
  const [sortMode, setSortMode] = useState("best"); // 선택된 정렬 옵션

  const setSortOption = (text) => {
    let mode;
    switch (text) {
      case "BEST":
        mode = "best";
        break;
      case "NEW":
        mode = "new";
        break;
      case "낮은 가격순":
        mode = "cheap";
        break;
      case "높은 가격순":
        mode = "expensive";
        break;
      default:
        mode = "best";
        break;
    }
    setSortMode(mode);
    setSortModeText(text);
    setToggleSorter(false);
  };

  const parts = new Array(14).fill(0);

  return (
    <div className={`${toggle_sort ? "h-screen overflow-hidden" : null}`}>
      <div
        className="z-10 mt-[12px] mb-[20px] text-14px text-left font-medium text-[#BDBDBD] bg-white relative"
        onClick={() => {
          setToggleSorter((cur) => !cur);
        }}
      >
        <div className="mx-[16px]">
          <span>
            액세서리{" "}
            <FontAwesomeIcon icon={faChevronRight} className="text-10px" />
          </span>
          &nbsp;
          <span className="font-bold text-black">
            {sortModeText}&nbsp;
            <FontAwesomeIcon icon={faChevronDown} className=" text-10px" />
          </span>
        </div>
        {toggle_sort ? (
          <div
            className="absolute top-[20px] z-10 w-full max-w-sm bg-white"
            onClick={(e) => {
              e.stopPropagation();
            }}
          >
            <div className="w-full max-w-sm py-[12px]">
              {sortOptions.map((opt, itr) => (
                <div
                  className="py-[12px]"
                  key={"liquid_sort_" + itr}
                  onClick={() => {
                    if (sortModeText === opt) setToggleSorter(false);
                    else setSortOption(opt);
                  }}
                >
                  <p
                    style={sortModeText === opt ? { color: "black" } : null}
                    className="px-[16px]"
                  >
                    {opt}
                  </p>
                </div>
              ))}
            </div>
          </div>
        ) : null}
      </div>

      <hr className="border-[#F2F2F2]" />

      <div className="mt-[30px] mx-[16px] text-left text-14px grid grid-cols-2 gap-[12px]">
        {parts.map((part, i) => (
          <div className="mb-[12px] flex flex-col" key={"shop_liquid_" + i}>
            <div className="bg-[#F5F5F5] w-full relative">
              <p className="my-[12px]">
                <img
                  src="/imageDB/pot_trace/vthru-pro-0.7.png"
                  width={138}
                  className="mx-auto"
                  alt=""
                />
              </p>
            </div>
            <div className="mt-3 text-left tracking-[-0.28px]">
              <p className="text-14px font-medium">부푸</p>
              <p className="text-14px">브이쓰루 0.7</p>
              <p className="font-bold">10억원</p>
              <p className="mt-[4px] py-[1px] px-[4px] text-10px font-bold w-min bg-[#E6F0FF] text-[#0060F1]">
                하루배송
              </p>
            </div>
          </div>
        ))}
      </div>

      {toggle_sort ? (
        // 음영
        <div
          className="absolute top-[84px] w-full max-w-sm h-full bg-[rgba(28,28,28)]/80"
          onClick={() => setToggleSorter(false)}
        />
      ) : null}
    </div>
  );
}
