// Swiper 라이브러리
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper";
import "swiper/css";
import { useNavigate } from "react-router-dom";

const ph_image = "/imageDB/liq_trace/gangjuice.png";

export default function Curation_Liq() {
  const nav = useNavigate();

  let liqs = [
    {
      manuf: "노보",
      name: "엠에스 블랜드",
      img: "/imageDB/liq_trace/Novo-MSBlend.png",
      price: 20000,
    },
    {
      manuf: "스톰리퀴드",
      name: "로얄 오크",
      img: "/imageDB/liq_trace/Storm-loyaloak.png",
      price: 20000,
    },
  ];
  for (let j = 0; j < 10; j++) {
    liqs.push({
      manuf: `제조${j}`,
      name: `이름${j}`,
      img: ph_image,
      price: 100,
    });
  }
  let icon = ["🚬"];
  let prefer = ["연초"];
  let season = ["가을"];

  return (
    <div className="my-[40px]">
      <div className="mx-[16px]">
        <div className="flex justify-between relative">
          <p className="font-medium">
            {icon}
            {prefer}향이 좋은 당신에게
          </p>
          <p
            className="text-[#A8A8A8] font-medium text-14px absolute -top-[8px] -right-[8px] p-[8px]"
            onClick={() => nav("/shop/liquid")}
          >
            더보기
          </p>
        </div>
        <div className="mt-[5px] text-[#A8A8A8] text-left text-14px">
          {season}에 어울리는 {prefer}향 액상
        </div>
      </div>
      <Swiper
        className="mySwiper mt-[24px]"
        width={138}
        slidesOffsetBefore={16}
        slidesOffsetAfter={-16}
        spaceBetween={12}
        modules={[Pagination]}
      >
        {liqs.length > 0 ? (
          liqs.map((liq, i) => (
            <SwiperSlide
              key={"liq_" + i}
              className="min-h-[166px] curation-item flex flex-col"
            >
              <div className="bg-[#F5F5F5] w-full">
                <p className="w-full right-[7.87px] text-right absolute">
                  {/* 지금은 찜 유무를 랜덤으로 뒀음.
                 나중에 세션으로 유저 정보 -> 찜 정보 받아올 수 있을 때
                 활성화해서 사용하시오 */}
                  {Math.round(Math.random()) ? (
                    <img
                      src="/imageDB/common/heartFull.svg"
                      width={14.26}
                      height={12.5}
                      className="absolute top-[9.5px] right-[7.87px]"
                      alt="찜한 상품"
                    />
                  ) : (
                    <img
                      src="/imageDB/common/heartEmpty.svg"
                      width={14.26}
                      height={12.5}
                      className="absolute top-[9.5px] right-[7.87px]"
                      alt="찜하지 않은 상품"
                    />
                  )}
                </p>
                <p className="my-[12px]">
                  <img src={liqs[i].img} width={138} alt="" />
                </p>
              </div>
              <div className="mt-3 text-left tracking-[-0.28px]">
                <p className="text-14px font-medium">{liqs[i].manuf}</p>
                <p className="text-14px">{liqs[i].name}</p>
                <p className="font-bold">
                  {liqs[i].price.toLocaleString("ko-KR")}원
                </p>
                <p className="mt-[4px] py-[1px] px-[4px] text-10px font-bold w-min bg-[#E6F0FF] text-[#0060F1]">
                  하루배송
                </p>
              </div>
            </SwiperSlide>
          ))
        ) : (
          <SwiperSlide className="min-h-[197px] mb-[16px] bg-[#F5F5F5]">
            장바구니가 비어있어요!
          </SwiperSlide>
        )}
      </Swiper>
    </div>
  );
}
