import { memo } from "react";
import { useNavigate } from "react-router-dom";
import HeadShop from "./HeadShop";

const Header = () => {
  const href = window.location.href;
  const nav = useNavigate();

  const HeadLanding = () => (
    <div
      className="pl-[28px] w-4/12"
      onClick={() => {
        nav(-1);
      }}
    >
      <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
    </div>
  );

  const HeadMain = () => (
    <div className="mx-auto">
      <img src="/img/ANDO.svg" width={56} className="mx-auto" alt="ANDO-Logo" />
      {/* <p className="text-12px text-[#777777]">금연으로 가는 다리</p> */}
    </div>
  );

  const HeadHistory = () => (
    <>
      <div
        className="w-4/12 text-left pl-[28px]"
        onClick={() => {
          nav(-1);
          window.scrollTo(0, 0);
        }}
      >
        <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
      </div>

      <div className="w-4/12 text-18px">지난 구매내역</div>
      <div className="w-4/12"></div>
    </>
  );

  const HeadLogo = () => (
    <div
      className="mx-auto"
      onClick={() => {
        nav("/");
      }}
    >
      <img src="/img/ANDO.svg" width={56} className="mx-auto" alt="ANDO-Logo" />
      {/* <p className="text-12px text-[#777777]">금연으로 가는 다리</p> */}
    </div>
  );

  const HeadReview = () => (
    <>
      <div
        className="w-4/12 text-left pl-[28px]"
        onClick={() => {
          nav(-1);
        }}
      >
        <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
      </div>

      <div
        className="w-4/12"
        onClick={() => {
          nav("/");
        }}
      >
        리뷰
      </div>
      <div className="w-4/12"></div>
    </>
  );

  const HeadElse = () => (
    <>
      <div
        className="w-4/12 text-left pl-[28px]"
        onClick={() => {
          nav(-1);
        }}
      >
        <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
      </div>

      <div
        className="w-4/12"
        onClick={() => {
          nav("/");
        }}
      >
        <img
          src="/img/ANDO.svg"
          width={56}
          className="mx-auto"
          alt="ANDO-Logo"
        />
        {/* <p className="text-12px text-[#777777]">금연으로 가는 다리</p> */}
      </div>
      <div className="w-4/12"></div>
    </>
  );

  return (
    <header
      // className="h-[82px] pt-[22px] pb-[20px] flex items-center"
      className="h-[72px] pt-[22px] pb-[10px] flex items-center"
      onClick={(e) => {
        e.stopPropagation();
      }}
    >
      {/* 이 부분 배포 시에는 변경해야 함. 특히 "href.endsWith("/")" 부분. */}
      {href.endsWith("landing") ? null : href.includes("landing") ? (
        <HeadLanding />
      ) : href.endsWith("http://localhost:3000/") || href.endsWith("/") ? (
        <HeadMain />
      ) : href.includes("history") ? (
        <HeadHistory />
      ) : href.endsWith("shop") ? (
        <HeadLogo />
      ) : href.includes("shop") ? (
        <HeadShop />
      ) : href.endsWith("my") ? (
        <HeadLogo />
      ) : href.includes("review") ? (
        <HeadReview />
      ) : (
        <HeadElse />
      )}
    </header>
  );
};

export default memo(Header);
